@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
}

body {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* stylelint-disable linebreaks */
.page-login {
  align-items: center;
  background-image: url(/recipes-app/static/media/background.0a2607ce.jpg);
  background-position: center;
  background-size: cover;
  display: flex;
  height: 100vh;
  justify-content: center;
  width: 100vw;
}

.login-box {
  align-items: center;
  color: white;
  display: flex;
  flex-direction: column;
  padding: 25px;
}

.login-box p {
  font-size: 1.5em;
  font-weight: 700;
  margin: 5px 0;
  text-shadow: 2px 2px black;
}

.password {
  position: relative;
}

.login-box input {
  border: none;
  border-radius: 3px;
  font-size: 1.3em;
  height: 4vh;
  padding: 5px;
  width: 100%;
}

.login-box label {
  width: 100%;
}

.login-box a {
  display: grid;
  place-items: center;
  text-decoration: none;
  width: 100%;
}

.password-button {
  position: absolute;
  z-index: 5;
  top: 60%;
  right: 2%;
  border: none;
  font-size: 1.5em;
  background-color: transparent;
}

.login-bot {
  background-color: red;
  border: none;
  border-radius: 2px;
  color: white;
  cursor: pointer;
  display: grid;
  font-size: 1.3em;
  font-weight: 700;
  height: 4vh;
  margin-top: 15px;
  place-items: center;
  width: 100%;
}

.login-bot:hover {
  background-color: rgb(161, 0, 0);
}

.login-bot:disabled {
  background-color: rgb(252, 87, 87);
}

/* stylelint-disable linebreaks */
.cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-bottom: 10vh;
  width: 100vw;
}

.category-active {
  background-color: #881412 !important;
}

.card {
  align-items: center;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5px 10px;
  transition: 0.5s ease;
  width: 300px;
  height: 300px;
}

.card:hover {
  background-color: rgba(0, 0, 0, 0.2);
}

.cards-container img {
  height: 80%;
  padding: 5px;
  width: 100%;
}

.buttons {
  bottom: 0;
  position: fixed;
}

.carrousel {
  display: flex;
  height: 400px;
  width: 100vw;
  margin-bottom: 5vh;
  position: relative;
  transition: 2s ease;
}

.carrousel a {
  height: 80%;
  display: grid;
  color: black;
  text-decoration: none;
  place-items: center;
}

.carrousel div {
  cursor: pointer;
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 300px;
  width: 300px;
  transition: .5s ease;
}

.carrousel div:hover {
  width: 310px;
  height: 310px;
  z-index: 5;
}

.carrousel div img {
  width: 100%;
}

.footer {
  align-items: center;
  background-color: #6b0504;
  border: 2px solid black;
  bottom: 0;
  display: flex;
  height: 8vh;
  justify-content: center;
  padding: 0 25px;
  position: fixed;
  text-align: center;
  width: 100%;
}

.footer button {
  display: grid;
  place-items: center;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  height: 64px;
  width: 64px;
  padding: 5px;
  margin: 0 25px;
  transition: 0.5s ease;
}

.footer button:hover {
  background-color: rgb(151, 151, 151);
}

/* stylelint-disable linebreaks */
.header {
  align-items: center;
  background-color: #6b0504;
  color: white;
  display: flex;
  height: 15vh;
  justify-content: space-between;
  padding: 50px;
  width: 100vw;
}

.round-button {
  background-color: transparent;
  border: none;
  border-radius: 50%;
  color: white;
  cursor: pointer;
  font-size: 2em;
  height: 48px;
  width: 48px;
}

.categories-buttons {
  align-items: center;
  display: flex;
  height: 5vh;
  justify-content: space-around;
  margin-bottom: 3vh;
  width: 100vw;
}

.input-container button {
  background-color: #cc1613;
  border: none;
  border-radius: 3px;
  color: white;
  cursor: pointer;
  padding: 5px;
}

.categories-buttons button {
  background-color: #b41614;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 1.3em;
  height: 100%;
  transition: 0.5s ease;
  width: 100%;
}

.categories-buttons button:hover {
  background-color: #881412;
}

.input-container {
  align-items: center;
  background-color: #6b0504;
  color: white;
  display: flex;
  flex-direction: column;
  height: 10vh;
  width: 100vw;
}

.input-container button:hover {
  background-color: #9c1a18;
}

.input-container label {
  margin-left: 5px;
  margin-right: 10px;
}

.input-text {
  align-items: center;
  display: flex;
  height: 60%;
  justify-content: center;
  width: 100vw;
}

.input-text input {
  border-radius: 3px;
  font-size: 1.4em;
  height: 4vh;
  padding: 5px;
  text-align: center;
  width: 80%;
}

.cards-container a {
  color: black;
  text-decoration: none;
}

@media screen and ( max-width : 480px ) {

  .categories-buttons button {
    background-color: #b41614;
    border: none;
    color: white;
    cursor: pointer;
    font-size: 0.8em;
    height: 100%;
    transition: 0.5s ease;
    width: 100%;
  }
}

.profile-page {
  width: 100vw;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

a {
  text-decoration: none;
}

.profile-container {
  width: 40%;
  height: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 2px solid #cc1613;
  border-radius: 30px;
  padding: 25px;
}

.profile-icon {
  display: grid;
  place-items: center;
  width: 120px;
  height: 120px;
  color: #cc1613;
  border: 2px solid #cc1613;
  border-radius: 50%;
  font-size: 5em;
  margin-bottom: 3vh;
  overflow: hidden;
}

.profile-icon img {
  width: 100%;
  height: 100%;
}

.button-container {
  margin-top: 2vh;
  width: 50%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.profile-button {
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  padding: 5px;
  background-color: transparent;
}

.profile-edit-icon {
  display: flex;
  width: 80%;
  justify-content: space-around;
  align-items: center;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.profile-page label {
  width: 50%;
  margin-bottom: 15px;
}

.edit {
  color: #2f4ac1;
  border: 2px solid #2f4ac1;
  margin-bottom: 2vh;
}

.recipes-done {
  color: #2fc18c;
  border: 2px solid #2fc18c;
}

.recipes-favorite {
  color: #cc1613;
  border: 2px solid #cc1613;
}

.exit-button {
  padding: 8.5px;
  color: red;
  border: 2px solid red;
}

.explorar-page {
  width: 100vw;
  height: 80vh;
  display: flex;
}

.explorar {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-position: center;
  background-size: 100%;
  height: 100%;
  width: 100%;
  font-size: 2em;
  transition: 2s ease;
}

.ex-food {
  font-size: 1.5em;
}

.explorar:hover {
  background-size: 110%;
}

.explorar svg {
  font-size: 3em;
}

.food {
  background-image: url(/recipes-app/static/media/food-background.29c6b32d.jpg);
}

.drink {
  background-image: url(/recipes-app/static/media/drink-background.b0d54d92.jpg);
}

.food-ingredint {
  background-image: url(/recipes-app/static/media/food-ingredients.c43c8545.jpg);
}

.drink-ingredient {
  background-image: url(/recipes-app/static/media/vodka.717c7334.jpg);
}

.area-location {
  background-image: url(/recipes-app/static/media/area-location.16197948.jpg);
}

.imLucky-food {
  background-image: url(/recipes-app/static/media/imLucky-food.d62a69a4.jpg);
}

.imLucky-drink {
  background-image: url(/recipes-app/static/media/imLucky-drink.fb2c4c4c.jpg);
}

.dropdown-container {
  width: 100vw;
  height: 5vh;
  display: grid;
  place-items: center;
}

.dropdown-container select {
  text-align: center;
  font-size: 1.2em;
  width: 200px;
  height: 4vh;
}

.explorar-page a {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-shadow: 1px 1px 5px black;
  text-decoration: none;
  color: white;
}
.details-title {
  width: 100vw;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  text-shadow: 2px 2px 5px black;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.details-title h1 {
  font-size: 3em;
}

.details-title img {
  margin-left: 5vw;
  height: 80%;
  border-radius: 30px;
}

.container {
  padding-top: 3vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #F0F0F0;
}

.information-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70%;
}

.prev {
  left: 2%;
}

.next {
  right: 3%;
}

.carrousel-bot:active {
  width: 62px;
  height: 62px;
}

.carrousel-bot {
  display: grid;
  place-items: center;
  top: 30%;
  position: absolute;
  z-index: 6;
  width: 64px;
  height: 64px;
  font-size: 3em;
  color: white;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  border: 2px solid white;
  box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.3);
  background-color: transparent;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px 25px;
  width: 70%;
  background-color: white;
  margin: 15px;
  border-radius: 15px;
}

.info iframe {
  width: 80%;
}

.info h2 {
  text-align: center;
  color: #cc1613;
  margin-bottom: 10px;
}

.start-recipe-button {
  padding: 10px 15px;
  background-color: #cc1613;
  color: white;
  border-radius: 6px;
  border: none;
  cursor: pointer;
  box-shadow: 5px 5px rgba(0, 0, 0, 0.2);
  transition: .2s ease;
}

.checkboxes-ingredients-container input {
  margin-right: 5px;
}

.start-recipe-button:disabled {
  cursor: auto;
  box-shadow: none;
  background-color: #a84e4d;
}

.start-recipe-button:active {
  box-shadow: none;
}

.bot-container {
  width: 100%;
  display: flex;
  justify-content: center;
}

@media screen and (max-width: 375px) {
  .carrousel {
    overflow: auto;
  }

  .carrousel-bot {
    display: none;
  }

  .details-title img {
    height: auto;
    width: 80%;
  }

  .info iframe {
    width: 90%;
    height: 40%;
  }

  .details-title {
    flex-direction: column-reverse;
    height: 80vh;
  }
  
  .information-container {
    width: 100%;
  }

  .info {
    width: 100%;
  }
}

.checkboxes-ingredients-container {
  display: flex;
  flex-direction: column;
}

